.signupBox {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: rgb(235, 235, 235);
  z-index: -1;
}
.infoBox {
  text-align: left;
}
.infoBox p {
  margin: 13px;
}
.infoBox p:nth-last-of-type(2),
.passwordText {
  font-size: 13px;
  color: rgb(82, 82, 82);
}
.infoBox p:nth-last-of-type(1) {
  font-size: 13px;
  color: #7387fc;
}
.signup {
  text-align: left;
  border-radius: 8px;
  padding: 20px;
  margin-left: 50px;
}
.signup input,
.signup label {
  width: 370px;
  margin: 5px auto;
  font-size: 15px;
  font-weight: bold;
  height: 35px;
}
.signup input {
  border: 1px solid gray;
  padding-left: 10px;
}
.chakeboxBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}
.chakeboxBox input {
  margin: 0px;
  padding: 0px;
  width: 50px;
  height: 50px;
}
.chakeboxBox span {
  font-size: 13px;
  color: rgb(82, 82, 82);
  width: 300px;
  margin: 0px 10px;
}
.endText {
  width: 300px;
  font-size: 13px;
  color: rgb(82, 82, 82);
  margin-top: 10px;
}
.Login {
  margin-top: 10px;
  text-align: center;
  font-size: 14px;
}
.Login button {
  color: #7387fc;
  border: none;
  background-color: transparent;
}
.Error {
  color: red;
  font-size: 13px;
  text-align: center;
}
@media only screen and (max-width: 600px) {
  .infoBox {
    display: none;
  }
  .signupBox {
    width: 100%;
    padding: 0px;
    margin: 0px;
  }
  .signup input,
  .signup label {
    width: 100%;
  }
  .chakeboxBox {
    display: none;
  }
  .passwordText {
    display: none;
  }
  .signup {
    width: 100%;
    text-align: center;
    margin: 0px;
    margin-top: 200px;
    height: 90vh;
  }
}
